/* ==========================================================================
   Stock Photography Single page style - begin
   ========================================================================== */

.mkdf-stock-photography-single-holder {
    @include mkdfRelativeHolderLayout();

    .mkdf-stock-photography-section-top {
        padding: 0 60px;
        box-sizing: border-box;
    }

    .mkdf-stock-photography-section-bottom {
        background-color: #f5f5f5;
        padding: 0 0 50px;
        .mkdf-container {
            background-color: transparent;
        }
    }

    .mkdf-sps-image-holder {
        @include mkdfRelativeHolderLayout();

        .mkdf-sps-image {
            @include mkdfRelativeHolderLayout();
            box-sizing: border-box;

            a, img {
                position: relative;
                display: block;
                margin: 0 auto;
            }
        }
    }

    .mkdf-sps-info-holder {
        @include mkdfRelativeHolderLayout();
    }
}

/* ==========================================================================
   Stock Photography Single page style - end
   ========================================================================== */