/* ==========================================================================
# Team list - begin
	========================================================================== */

.mkdf-team-list-holder {
    @include mkdfRelativeHolderLayout();

    .mkdf-team {
        @include mkdfRelativeHolderLayout();

        float: left;
        box-sizing: border-box;
    }

    /***** Team Columns Space - begin *****/

    $columns_space_label: ('no', 'tiny', 'small', 'normal');
    $columns_space: (0, 5, 10, 15);

    @for $i from 0 to length($columns_space_label) {
        &.mkdf-tl-#{nth($columns_space_label,$i+1)}-space {
            $item_space: nth($columns_space, $i+1);
            $item_bottom_margin: $item_space * 2;

            .mkdf-tl-inner {
                @if($item_space == 0) {
                    margin: 0;
                } @else {
                    margin: 0 -#{$item_space}px;
                }
            }

            .mkdf-team {
                padding: 0 #{$item_space}px;
                margin: 0 0 #{$item_bottom_margin}px;
            }
        }
    }

    /***** Team Columns Space - end *****/

    /***** Team Columns - begin *****/

    $team_columns_number: ('two', 'three', 'four', 'five');

    @for $i from 0 to length($team_columns_number) {
        &.mkdf-tl-#{nth($team_columns_number,$i+1)}-columns {

            .mkdf-team {
                width: 100% / ($i+2);
            }

            @if ($i > 1) { // set different break point for four and five columns
                @media only screen and (min-width:  $laptop-landscape-plus-pixel) {

                    .mkdf-team {

                        &:nth-child(#{$i+2}n+1) {
                            clear: both;
                        }
                    }
                }
            } @else {
                @media only screen and (min-width:  $ipad-landscape-plus-pixel) {

                    .mkdf-team {

                        &:nth-child(#{$i+2}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }

    /***** Team Columns - end *****/
}

/* ==========================================================================
# Team list - end
========================================================================== */