.mkdf-stock-photography-single-holder {
    &.mkdf-stock-photography-single-standard {
        .mkdf-sps-content-holder {
            margin-top: 75px;
        }
        .mkdf-sps-author-description {
            .mkdf-author-description-content {
                @include mkdfTableLayout();
            }
            .mkdf-author-description-image {
                @include mkdfTableCellLayout();
                width: 78px;
                vertical-align: top;
            }
            .mkdf-author-description-text-holder {
                @include mkdfTableCellLayout();
                padding-left: 15px;
                vertical-align: top;
            }
        }

        .mkdf-sps-author-description {
            margin-bottom: 25px;
        }
    }
}