/* ==========================================================================
   Stock Photography Item Layout - Standard style - begin
   ========================================================================== */

.mkdf-stock-photography-list-holder {

	&.mkdf-sp-standard {

		article {
			&:hover {

				.mkdf-spi-text-holder {
					opacity: 1;
				}
			}

            .mkdf-spi-text-holder {
                @include mkdfAbsoluteHolderLayout();
                padding: 20px;
                background-color: rgba($default-heading-color, 0.4);
                opacity: 0;
                text-align: center;
                box-sizing: border-box;
                @include mkdfTransition(opacity .2s ease-in-out);
            }

            .mkdf-spi-cart,
            .mkdf-spi-favorites,
            .mkdf-spi-zoom {
                position: relative;
                z-index: 99;
                display: inline-block;
                padding-left: 8px;

                a {
                    font-size: 20px;
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
            .mkdf-spi-cart {
                padding-left: 0;
            }
		}
	}
}
/* ==========================================================================
   Stock Photography Item Layout - Standard style - end
   ========================================================================== */