/* ==========================================================================
   Stock Photography shortcode style - begin
   ========================================================================== */

.mkdf-stock-photography-list-holder {
    @include mkdfRelativeHolderLayout();

	/***** Article Global Style - begin *****/

    article {
	    @include mkdfRelativeHolderLayout();
        box-sizing: border-box;

	    .touch & {
		    cursor: pointer;
	    }

	    .mkdf-sp-item-inner {
		    @include mkdfRelativeHolderLayout();
		    overflow: hidden;
	    }
	    
	    .mkdf-spi-image {
		    @include mkdfRelativeHolderLayout();
		    
		    img {
			    display: block;
			    width: 100%;
		    }
	    }

	    .mkdf-spi-link {
		    @include mkdfAbsoluteHolderLayout();
	    }

	    .mkdf-spi-text-wrapper {
	        @include mkdfTableLayout();
	    }

	    .mkdf-spi-text {
		    @include mkdfTableCellLayout();
	    }
    }

	/***** Article Global Style - end *****/
	
	/***** Specific Global Style - begin *****/
	
	&.mkdf-sp-has-filter {
		
		article {
			
			.mkdf-sp-inner {
				overflow: hidden;
			}
		}
	}
    
	/***** Specific Global Style - end *****/
	
	/***** Stock Photography Columns Space - begin *****/
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal');
	$columns_space: (0, 5, 10, 15);
	
	@for $i from 0 to length($columns_space_label) {
		&.mkdf-sp-#{nth($columns_space_label,$i+1)}-space {
			$item_space: nth($columns_space, $i+1);
			$item_bottom_margin: $item_space * 2;
			
			.mkdf-sp-inner {
				@if($item_space == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$item_space}px;
				}
			}
			
			article {
				padding: 0 #{$item_space}px;
				margin: 0 0 #{$item_bottom_margin}px;
			}
		}
	}
	
	/***** Stock Photography Columns Space - end *****/
	
	/***** Stock Photography Types - begin *****/
	
	&.mkdf-sp-gallery {
		
		&:not(.mkdf-sp-one-column) {
			
			article {
				float: left;
			}
		}
		
		$gallery_columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($gallery_columns_number) {
			&.mkdf-sp-#{nth($gallery_columns_number,$i+1)}-columns {
				
				article {
					width: 100% / ($i+2);
				}
				
				@if ($i > 1) { // set different break point for four and five columns
					@media only screen and (min-width: $laptop-landscape-plus-pixel) {
						
						article {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				} @else {
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						article {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
	}
	
	&.mkdf-sp-masonry {
		
		.mkdf-sp-inner {
			opacity: 0;
		}
		
		article,
		.mkdf-sp-grid-sizer {
			width: 100%;
		}
		
		.mkdf-sp-grid-gutter {
			width: 0;
		}
		
		$masonry_columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($masonry_columns_number) {
			&.mkdf-sp-#{nth($masonry_columns_number,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				article,
				.mkdf-sp-grid-sizer {
					width: $column_width;
				}
				
				article {
					
					&.mkdf-sp-masonry-large-width,
                    &.mkdf-sp-masonry-large-width-height {
						width: $column_width * 2;
					}
				}
			}
		}

        &.mkdf-sp-images-fixed {
	        
            article {
	            
                .mkdf-sp-item-inner {
                    height: 100%;
                }
	
	            .mkdf-spi-image {
		            height: 100%;
		
		            img {
			            height: 100%;
		            }
	            }
            }
        }
	}

    /*** Justified gallery start styles ***/

    &.mkdf-sp-justified-gallery {

        opacity: 0;
        height: auto!important;

        .mkdf-sp-inner {
            margin: 0 !important;
            width: 100%;
            position: relative;
            overflow: hidden;
        }

        article {
            position: absolute;
            display: inline-block;
            overflow: hidden;
            padding: 0 !important;
            margin: 0 !important;
            width: auto;

            .mkdf-item-image-holder {
                height: 100%;
                overflow: hidden;
            }

            .mkdf-spi-image {
                height: 100%;
                display: block;
                width: auto;
                overflow: hidden;
                img {
                    width: 100%;
                    vertical-align: middle;
                    max-height: 100%;
                }
            }
        }

    }

	/***** Stock Photography Types - end *****/

	/***** Additional Features - begin *****/

	&.mkdf-sp-has-animation {

		article {
			opacity: 0;
			@include mkdfTransform(translateY(80px));
			@include mkdfTransition(opacity .8s cubic-bezier(0.34, 0.52, 0.57, 1.04), transform .8s cubic-bezier(0.34, 0.52, 0.57, 1.04));

			&.mkdf-item-show {
				opacity: 1;
				@include mkdfTransform(translateY(0));

				&.mkdf-item-shown {
					@include mkdfTransition(none);
				}
			}
			
			.touch & {
				opacity: 1;
				@include mkdfTransform(translateY(0));
			}
		}
	}

	/***** Additional Features - end *****/
}

/* ==========================================================================
   Stock Photography shortcode style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography filter style - begin
   ========================================================================== */

.mkdf-sp-filter-holder {
    @include mkdfRelativeHolderLayout();
    margin: 0 0 30px;
    text-align: center;

    ul {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 0 10px;
	        padding: 0 22px;
            cursor: pointer;
	        
	        @include laptop-landscape-large {
		        padding: 0 16px;
	        }
	
	        @include ipad-landscape {
		        padding: 0 10px;
	        }

            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                color: $default-heading-color;
                font-size: 12px;
                line-height: 22px;
                white-space: nowrap;
                @include mkdfTransition(color .2s ease-out);
            }

            &.mkdf-sp-current,
            &:hover {

                span {
                    color: $first-main-color;
                }
            }
        }
    }
}
/* ==========================================================================
   Stock Photography filter style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography standard pagination style - begin
   ========================================================================== */

.mkdf-stock-photography-list-holder {
	
	&.mkdf-sp-pag-standard {
		
		.mkdf-sp-inner {
			opacity: 1;
			@include mkdfTransition(opacity .2s ease-out);
		}
		
		&.mkdf-sp-pag-standard-animate {
			
			.mkdf-sp-inner {
				opacity: 0;
			}
		}
	}
}

.mkdf-sp-standard-pagination {
	@include mkdfRelativeHolderLayout();
	margin: 20px 0 0;
	clear: both;
	
	ul {
		@include mkdfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 12px;
			padding: 0;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0;
				padding: 0;
			}
			
			&.mkdf-sp-pag-active {
				
				a {
					color: $first-main-color;
				}
			}
			
			&.mkdf-sp-pag-prev,
			&.mkdf-sp-pag-next {
				position: absolute;
				top: 0;
				
				a {
					font-size: 30px;
					@include mkdfTransition(color .2s ease-out, opacity .2s ease-out);
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			&.mkdf-sp-pag-prev {
				left: 0;
				
				a {
					opacity: 0;
				}
			}
			
			&.mkdf-sp-pag-next {
				right: 0;
				
				a {
					opacity: 1;
				}
			}
		}
	}
}
/* ==========================================================================
   Stock Photography standard pagination style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography load more pagination style - begin
   ========================================================================== */

.mkdf-sp-load-more-holder {
	@include mkdfRelativeHolderLayout();

    .mkdf-sp-load-more {
	    margin: 40px 0 0;
	    text-align: center;
    }
}
/* ==========================================================================
   Stock Photography load more pagination style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography loading element style - begin
   ========================================================================== */

.mkdf-sp-loading {
	position: relative;
	display: none;
	width: 100%;
	margin: 40px 0 20px;
	color: $default-heading-color;
	text-align: center;

	&.mkdf-filter-trigger {
		position: absolute;
		top: 250px;
		left: 0;
	}
	
    &.mkdf-standard-pag-trigger {
		position: absolute;
		top: 50px;
		left: 0;
	
	    .mkdf-sp-has-filter & {
		    top: 150px;
	    }
    }

	&.mkdf-showing {
		display: block;
	}

	> div {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 14px;
		height: 14px;
		margin: 0 3px;
		background-color: $default-heading-color;
		border-radius: 100%;
		@include mkdfAnimation(sk-bouncedelay 1.4s infinite ease-in-out both);
	}

	.mkdf-sp-loading-bounce1 {
		-webkit-animation-delay: -0.32s;
		-moz-animation-delay: -0.32s;
		animation-delay: -0.32s;
	}

	.mkdf-sp-loading-bounce2 {
		-webkit-animation-delay: -0.16s;
		-moz-animation-delay: -0.16s;
		animation-delay: -0.16s;
	}
}

@-webkit-keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
	}
}

@-moz-keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-moz-transform: scale(0);
	}
	40% {
		-moz-transform: scale(1.0);
	}
}

@keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
		transform: scale(1.0);
	}
}
/* ==========================================================================
   Stock Photography loading element style - end
   ========================================================================== */