/* ==========================================================================
   Masonry Gallery shortcodes style - begin
   ========================================================================== */

.mkdf-masonry-gallery-holder {
	@include mkdfRelativeHolderLayout();
	
	/***** Masonry Gallery Columns Space - begin *****/
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal');
	$columns_space: (0, 5, 10, 15);
	
	@for $i from 0 to length($columns_space_label) {
		&.mkdf-mg-#{nth($columns_space_label,$i+1)}-space {
			$item_space: nth($columns_space, $i+1);
			@if($item_space == 0) {
				margin: 0;
			} @else {
				margin: 0 -#{$item_space}px;
			}
			
			.mkdf-mg-inner {
				@if($item_space == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$item_space}px;
				}
			}
			
			.mkdf-mg-item {
				padding: #{$item_space}px;
			}
		}
	}
	
	/***** Masonry Gallery Columns Space - end *****/
	
	.mkdf-mg-inner {
		opacity: 0;
	}
	
	.mkdf-mg-grid-gutter {
		width: 0;
	}
	
	.mkdf-mg-grid-sizer {
		width: 25%;
	}

	.mkdf-mg-item {
		position: relative;
		width: 25%;
		z-index: 1;
		box-sizing: border-box;
		
		&.mkdf-mg-square-big {
			width: 50%;
		}

		&.mkdf-mg-rectangle-landscape {
			width: 50%;

			.mkdf-mg-image {
				
				img {
					max-height: inherit;
				}
			}
		}

		&.mkdf-mg-rectangle-portrait {
			
			.mkdf-mg-image {
				
				img {
					max-width: inherit;
					width: auto;
					height: 100%;
				}
			}
		}
		
		.mkdf-mg-content {
			@include mkdfRelativeHolderLayout();
			height: 100%;
			box-sizing: border-box;
		}
		
		.mkdf-mg-item-outer {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			text-align: center;
			box-sizing: border-box;
		}
		
		.mkdf-mg-item-inner {
			@include mkdfTableLayout();
		}
		
		.mkdf-mg-item-content {
			@include mkdfTableCellLayout();
			padding: 10px;
		}

		.mkdf-mg-image {
			position: relative;
			height: 100%;
			width: 100%;
			overflow: hidden;
			@include mkdfTransform(translateZ(0px));
		}
		
		.mkdf-mg-image-overlay {
			position: absolute;
			top: -5px;
			left: -5px;
			height: calc(100% + 10px);
			width: calc(100% + 10px);
			background-size: cover;
			background-position: center;
			z-index: -1;
		}
		
		.mkdf-mg-item-icon {
			margin: 0 0 1px;
		}
		
		.mkdf-mg-item-title {
			margin: 0;
		}
		
		.mkdf-mg-item-text {
			margin: 14px 0 0;
		}
		
		.mkdf-mg-item-button {
			margin: 21px 0 0;
		}
		
		.mkdf-mg-item-link {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
		}
		
		&.mkdf-mg-standard {
			
			.mkdf-mg-item-inner {
				opacity: 0;
				@include mkdfTransition(opacity .25s cubic-bezier(0.45, 0.05, 0.55, 0.95), background-color .25s cubic-bezier(0.45, 0.05, 0.55, 0.95));
			}
			
			.mkdf-mg-item-title,
			.mkdf-mg-item-text {
				color: #fff;
			}
			
			img {
				@include mkdfTransition(opacity .7s ease);
				@include mkdfTransform(translateZ(0));
			}
			
			&:hover {
				
				.mkdf-mg-item-inner {
					opacity: 1;
					background-color: rgba($default-heading-color, 0.4);
				}
				
				img {
					opacity: 0;
				}
			}
		}

		&.mkdf-mg-simple {
			
			&.mkdf-mg-skin-default {
				
				.mkdf-mg-item-inner {
					background-color: #f8f7f7;
				}
			}
			
			&.mkdf-mg-skin-light {
				
				.mkdf-mg-item-inner {
					background-color: #fff;
				}
			}
			
			&.mkdf-mg-skin-dark {
				
				.mkdf-mg-item-inner {
					background-color: $default-heading-color;
				}
				
				.mkdf-mg-item-title,
				.mkdf-mg-item-text {
					color: #fff;
				}
			}
		}
	}
}
/* ==========================================================================
   Masonry Gallery shortcodes style - end
   ========================================================================== */